import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { FormBase } from '../../model/form-base';
@Injectable()
export class FormService {

  constructor() { }

  toFormGroup(textBoxs: FormBase<any>[]) {
    const group: any = {};
    textBoxs.forEach(textbox => {
      group[textbox.key] = textbox.required ? new FormControl(textbox.value || '', Validators.required)
                                            : new FormControl(textbox.value || '');
    });
    return new FormGroup(group);
  }

}



