import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBase } from '../../../model/form-base';
import { FormGroup } from '@angular/forms';
import { FormService } from '../../form/form.service';
import { CategoryService } from '../category.service';
import { Category } from '../../../../interface/category.interface';
@Component({
  selector: 'app-category-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.css'],
  providers: [ CategoryService ],
  encapsulation: ViewEncapsulation.None
})
export class FormComponent implements OnInit {
  @Input() questions: FormBase<any>[] = [];
  form: FormGroup;
  payLoad = '';

  constructor(private formService: FormService, public categoryService: CategoryService) {  }

  ngOnInit() {
    this.form = this.formService.toFormGroup(this.questions);
  }

  onSubmit() {
    this.payLoad = JSON.stringify(this.form.value);
    this.categoryService.postCategory(<Category>this.form.value).subscribe(res => {
      console.log(JSON.stringify(res));
    });
  }

}



