import { Injectable } from '@angular/core';
import { FormBase } from '../../model/form-base';
import { FormDropdown } from '../../model/form-dropdown';
import { FormTextBox } from '../../model/form-textbox';
import { Response } from '../../../interface/response.interface';
import { Category } from '../../../interface/category.interface';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { catchError, map, tap } from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

const baseURI = `api/category`;

@Injectable()
export class CategoryService {
  getQuestions() {
       const questions: FormBase<any>[] = [
         new FormTextBox({
           key: 'name',
           label: 'Name',
           value: '',
           required: true,
           order: 1
         }),

         new FormTextBox({
           key: 'description',
           label: 'Description',
           value: '',
           order: 2
         })
       ];

       return questions.sort((a, b) => a.order - b.order);
     }
  constructor(private http: HttpClient) { }

  getCategories(): Observable<Response> {
    return this.http.get<Response>(baseURI);
  }

  getCategory(id: String): Category {
    this.getCategories().subscribe(categories => {
      return categories.items.find(c => c._id === id);
    });
    return null;
  }

  postCategory(category: Category): Observable<any> {
    return this.http.post<Category>(baseURI, category, httpOptions);
  }

  deleteCategory(category: Category): Observable<any> {
    return this.http.delete(`${baseURI}/${category._id}`, httpOptions);
  }

  udpateCategory(category: Category): Observable<any> {
    return this.http.put(`${baseURI}/${category._id}`, category, httpOptions);
  }

}



