import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { CarouselItem} from '../../../../model/carousel-item.model';

@Component({
  selector: 'app-heading',
  templateUrl: './heading.component.html',
  styleUrls: ['./heading.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class HeadingComponent implements OnInit {
  headingItems: CarouselItem[] = [
    new CarouselItem('Title 1', 'Description 1', 'Button 1'),
    new CarouselItem('Title 2', 'Description 2', 'Button 2'),
    new CarouselItem('Title 3', 'Description 3', 'Button 3'),
    new CarouselItem('Title 4', 'Description 4', 'Button 4'),
    new CarouselItem('Title 5', 'Description 5', 'Button 5'),
    new CarouselItem('Title 6', 'Description 6', 'Button 6')
  ];
  constructor() { }

  ngOnInit() {
  }

}



